<template>
    <div class="addAddress">
        <header>
            <a href="javascript:history.back(-1)" class="h_a_1">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>添加地址</span>
        </header>
        <section>
            <div class="s1">
                <span>联系人</span>
                <div class="s11">
                    <input type="text" placeholder="你的名字" v-model="name">
                    <div class="s12">
                        <span>
                            <i class="el-icon-success" @click="sex=1" :style="{'color':sex==1 ? '#4cd964' : '#ccc'}"></i>
                            <span>先生</span>
                        </span>
                        <span>
                            <i class="el-icon-success" @click="sex=2" :style="{'color':sex==2 ? '#4cd964' : '#ccc'}"></i>
                            <span>女士</span>
                        </span>
                    </div>
                </div>
            </div>
            <div class="s2">
                <span>联系电话</span>
                <div class="s21">
                    <div>
                        <input type="text" placeholder="你的手机号" v-model="phone">
                        <i class="el-icon-plus" @click="isShow = true"></i>
                    </div>
                    <input type="text" placeholder="备选电话" v-if="isShow" v-model="phone_bk">
                </div>
            </div>
            <div class="s3">
                <span>送餐地址</span>
                <div class="s31">
                    <input type="text" placeholder="小区/写字楼/学校等"  v-model="address" @click="toAddDetail">
                    <input type="text" placeholder="详细地址（如门牌号等）" v-model="address_detail">
                </div>
            </div>
            <div class="s4">
                <span>标签</span>
                <div class="s41">
                    <input type="text" placeholder="无/家/学校/公司" v-model="tags">
                </div>
            </div>
        </section>
        <div class="addAdd" @click="addAdd">确定</div>
    </div>
</template>

<script>
    export default {
        name: "AddAddress",
        data(){
            return{
                isShow:false,
                sex:1,
                input:[],
                addressS:[],
                user_id:"",
                address:"",
                address_detail:"",
                geohash:"",
                name:"",
                phone:"",
                tags:"",
                tag:"null",
                tag_type:"",
                phone_bk:"",
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id");
            if(localStorage.getItem("address")){
                this.addressS = JSON.parse(localStorage.getItem("address"));
                this.address = this.addressS.name;
                this.geohash = this.addressS.geohash;
            }
            if(localStorage.getItem("input")){
                this.input = JSON.parse(localStorage.getItem("input"))[0];
                this.name = this.input.name;
                this.sex = this.input.sex;
                this.address_detail = this.input.address_detail;
                this.phone = this.input.phone;
                this.phone_bk = this.input.phone_bk;
                this.tag_type = this.input.tag_type;
                localStorage.removeItem("input");
            }
        },
        methods:{
            addAdd(){
                switch (this.tags) {
                    case "家":{
                        this.tag_type = 2;
                        break;
                    }
                    case "学校":{
                        this.tag_type = 3;
                        break;
                    }
                    case "公司":{
                        this.tag_type = 4;
                        break;
                    }
                    // default:{
                    //     this.tag_type = "null"
                    // }
                }
                if(this.address!=[]&&this.address_detail!=""&&this.name!=""&&this.phone!="") {
                    this.axios.post("/v1/users/" + this.user_id + "/addresses", {
                        user_id: this.user_id,
                        address: this.address,
                        address_detail: this.address_detail,
                        geohash: this.geohash,
                        name: this.name,
                        phone: this.phone,
                        tag:this.tag,
                        sex: this.sex,
                        phone_bk: this.phone_bk,
                        tag_type: this.tag_type,
                    }).then(res => {
                        // console.log(res);
                        if (res.message != "地址信息错误") {
                            this.$router.go(-1);
                            localStorage.removeItem("address");
                        }
                    }).catch(err => {});
                }
            },
            toAddDetail(){
                this.$router.push({path:'/addDetail',query:{name:this.address}});
                let a = [];
                a.push({name:this.name,sex:this.sex,tag_type:this.tag_type,address_detail:this.address_detail,phone:this.phone,phone_bk:this.phone_bk});
                localStorage.setItem("input",JSON.stringify(a));
            }
        }
    }
</script>

<style scoped>
    .addAddress{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    section{
        background-color: #fff;
        padding: 0 .7rem;
    }
    .s1,.s2,.s3,.s4{
        display: flex;
        border-bottom: .025rem solid #f5f5f5;
    }
    .s1>span,.s2>span,.s3>span,.s4>span{
        font-size: .7rem;
        color: #333;
        flex: 2;
        line-height: 2.5rem;
    }
    .s11,.s21,.s31,.s41{
        flex: 5;
    }
    .s11 input,.s21 input,.s31 input,.s41 input{
        width: 100%;
        height: 2.5rem;
        font-size: .7rem;
        color: #999;
        outline: none;
        border:0;
    }
    .s12{
        display: flex;
        line-height: 2.5rem;
        border-top: .025rem solid #f5f5f5;
    }
    .s12>span{
        font-size: .7rem;
        color: #333;
        display: flex;
        align-items: center;
        margin-right: .8rem;
    }
    .s12>span>i{
        margin-right: .3rem;
        font-size: 0.9rem;
        color: #ccc;
    }
    .s21>div{
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: .025rem solid #f5f5f5;
    }
    .s21>div>i{
        color:deepskyblue;
        font-size: 1.1rem;
    }
    .s31 input:nth-child(1){
        border-bottom: .025rem solid #f5f5f5;
    }

    .addAdd{
        background-color: #4cd964;
        font-size: .7rem;
        color: #fff;
        text-align: center;
        margin: 0.6rem .7rem;
        line-height: 1.8rem;
        border-radius: .2rem;
    }
</style>